import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaColumn=null;
export default {
	data() {
		return {
			//顶部导航栏
			height:"",
			background:{
				backgroundImage: "linear-gradient(90deg, #54C3F1, #00ABEB)",
			},
			data:{},
			//顶部导航栏
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			serverData:'',
			count: 5,
			value: 4,
			show: false,
			mode: 'range'
		}
	},
	onLoad(event) {
		_self = this;
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
		// TODO 后面把参数名替换成 payload
		console.log(event.userdata);
		const payload = event.userdata || event.payload;
		// 目前在某些平台参数会被主动 decode，暂时这样处理。
		try {
			this.data= JSON.parse(decodeURIComponent(payload));
			
			console.log(this.data);
		} catch (error) {
			this.data = JSON.parse(payload);
			console.log(this.data);
		}
	},
	methods: {
		getServerData(){
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data:{
				},
				success: function(res) {
					console.log(res.data.data)
					//下面这个根据需要保存后台数据，我是为了模拟更新柱状图，所以存下来了
					_self.serverData=res.data.data;
					let Column={categories:[
						 "4",
						 "5",
						 "6",
						 "7",
						 "8",
						 "9",
					],series:[
						{
						  name: "完成订单",
						  data: [15, {
						    value: 20,
						    color: "#f04864"
						  }, 45, 37, 43, 34]
						}, 
						{
						  name: "结算金额",
						  "data": [30, {
						    "value": 40,
						    "color": "#facc14"
						  }, 25, 14, 34, 18]
						},
						{
						  name: "取消订单",
						  "data": [30, {
						    "value": 40,
						    "color": "#facc14"
						  }, 25, 14, 34, 18]
						},
					]};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					// Column.categories=res.data.data.Column.categories;
					// Column.series=res.data.data.Column.series;
					_self.showColumn("canvasColumn",Column);
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		showColumn(canvasId,chartData){
			canvaColumn=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'column',
				legend:{show:true},
				fontSize:11,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				animation: true,
				categories: chartData.categories,
				series: chartData.series,
				xAxis: {
					disableGrid:true,
				},
				yAxis: {
					//disabled:true
				},
				dataLabel: true,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					column: {
						type:'group',
						width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length
					}
				  }
			});
			
		},
		touchColumn(e){
			canvaColumn.showToolTip(e, {
				format: function (item, category) {
					if(typeof item.data === 'object'){
						return category + ' ' + item.name + ':' + item.data.value 
					}else{
						return category + ' ' + item.name + ':' + item.data 
					}
				}
			});
		},
		change(e) {
			console.log(e);
		}
	}
}
